import React, { useEffect, useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';

import './assets/css/tailwind.css';
import './assets/css/icons.css';

import Index from './pages/index.js';
import Aboutus from './pages/aboutus';
import Login from './pages/auth/login';
import Signup from './pages/auth/signup';
import Terms from './pages/terms';
import Privacy from './pages/privacy';
import Contact from './pages/contact';
import BlogDetail from './pages/blog-detail';
import Blogs from './pages/blogs';
import Faq from './pages/faq';
import ScrollToTop from './component/Scroll-top';
import Services from './pages/services.js';
import Resources from './pages/resources.js';
import Testimonials from './pages/testimonials.js';
import Careers from './pages/careers.js';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './App.css';
import Admin from './pages/admin.js';
import axios from 'axios';
import { baseURL } from './utils.js';
function App() {
  const [pages, setPages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [errorSMS, setErrorSMS] = useState('Loading ...');

  function transformArrayToObject(arr) {
    const result = {};
    arr.forEach(item => {
      result[item.page] = item;
    });
    return result;
  }

  useEffect(() => {

    initData();
  }, []);
  const initData = async () => {
    try {
      const pages = (await axios.get(`${baseURL}/api/pages`)).data;
      console.log(pages);
      const result = transformArrayToObject(pages);
      console.log(result);
      setPages(result);

      setLoading(false);
    } catch (error) {
      toast.error('Error , Please Try later ');
    }
  };
  useEffect(() => {
    document.documentElement.setAttribute('dir', 'ltr');
    document.documentElement.classList.add('light');
  }, []);

  return (
    <div>
      <ToastContainer />
      <BrowserRouter>
        <ScrollToTop />
        {loading ? (
          <h3
            style={{
              textAlign: 'center',
              fontSize: '22px',
              marginTop: '200px',
            }}
          >
            {loading}
          </h3>
        ) : (
          <Routes>
            <Route path="/" element={<Index content={pages} fetchData={initData} />} />
            <Route path="/about-us" element={<Aboutus content={pages} fetchData={initData} />} />
            <Route path="/services" element={<Services content={pages} fetchData={initData} />} />
            <Route path="/resources" element={<Resources content={pages} fetchData={initData} />} />
            <Route path="/faq" element={<Faq content={pages} fetchData={initData} />} />
            <Route path="/contact" element={<Contact content={pages} fetchData={initData} />} />
            <Route path="/auth-login" element={<Login content={pages} fetchData={initData} />} />
            <Route path="/auth-signup" element={<Signup content={pages} fetchData={initData} />} />
            <Route
              path="/testimonials"
              element={<Testimonials content={pages} fetchData={initData} />}
            />
            <Route path="/blogs" element={<Blogs content={pages} fetchData={initData} />} />
            <Route
              path="/blog-detail"
              element={<BlogDetail content={pages} fetchData={initData} />}
            />
            <Route path="/terms" element={<Terms content={pages} fetchData={initData} />} />
            <Route path="/privacy" element={<Privacy content={pages} fetchData={initData} />} />
            <Route path="/careers" element={<Careers content={pages} fetchData={initData} />} />

            {/* Admin Routes */}
            <Route path="/admin" element={<Admin content={pages} fetchData={initData} />} />
          </Routes>
        )}
      </BrowserRouter>
    </div>
  );
}
export default App;
