import React, { useEffect, useState } from 'react';
import { Link as Link2 } from 'react-router-dom';
import Navbar from '../component/Navbar';

import BackgroudImage from '../assets/images/bg/01.jpg';
import { baseURL } from '../utils';
import axios from 'axios';
import { toast } from 'react-toastify';

export default function Admin({ content, fetchData }) {
  const [showInput, setShowInput] = useState();

  const [images, setImages] = useState();
  const [pageName, setPage] = useState();
  const [text, setTexts] = useState();
  const [sectionName, setSectionName] = useState();
  const [allUser, setAllUser] = useState([]);

  const [creatingPage, setCreateNewPage] = useState(false);
  const [file, setFile] = useState(null);
  const [message, setMessage] = useState('');

  useEffect(() => {
    getAllUser();
  }, []);

  const getAllUser = async () => {
    try {
      const allUser = await axios.get(`${baseURL}/api/sub`);
      console.log('alluser', allUser.data);
      setAllUser(allUser.data);
    } catch (error) {
      console.log(error);
    }
  };

  const updateSection = async (page, section) => {
    try {
      const updateInfo = {
        page,
        section,
        img: images,
        txt: text,
      };
      await axios.put(`${baseURL}/api/pages/page/section`, updateInfo);
      fetchData();
      toast.success('Updated Section');
    } catch (error) {
      console.log(error);
    }
  };

  const createPage = async e => {
    e.preventDefault();
    try {
      const info = {
        page: pageName,
      };
      await axios.post(`${baseURL}/api/pages`, info);
      fetchData();
      toast.success('Page Created !!');
    } catch (error) {
      console.log(error);
    }
  };

  const createSection = async e => {
    e.preventDefault();

    const obj = {
      page: pageName,
      section: sectionName,
    };
    try {
      await axios.post(`${baseURL}/api/pages/page/section`, obj);
      fetchData();
      toast.success('Section Created !!');
    } catch (error) {
      console.log(error);
    }
  };

  const onFileChange = e => {
    const selectedFile = e.target.files[0];
    if (selectedFile && selectedFile.type.startsWith('image/')) {
      setFile(selectedFile);      
      submitImage()
    } else {
      setMessage('Please select an image file.');
    }
  };

  const submitImage = async e => {
    if (!file) {
      setMessage('No file selected');
      return;
    }

    const formData = new FormData();
    formData.append('file', file);

    try {
      setMessage("Uploading ...")
      const res = await axios.post(
        'http://157.230.188.234:4000/upload',
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }
      );
      setImages([...images, res.data.filename])
    } catch (err) {
      setMessage('Error uploading file');
    }
  };
  return (
    <>
      <Navbar navClass="navbar-white" />
      <section
        style={{ backgroundImage: `url(${BackgroudImage})` }}
        className="relative table w-full py-32 lg:py-36 bg-no-repeat bg-center bg-cover"
      >
        <div className="absolute inset-0 bg-black opacity-80"></div>
        <div className="container">
          <div className="grid grid-cols-1 text-center mt-10">
            <h3 className="md:text-4xl text-3xl md:leading-normal leading-normal font-medium text-white">
              Admin
            </h3>
            <p style={{ color: 'white' }}>
              Create and Manage your Website Content-Admin Access
            </p>
          </div>
        </div>
      </section>
      <div className="relative">
        <div className="shape overflow-hidden z-1 text-white dark:text-slate-900">
          <svg
            viewBox="0 0 2880 48"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0 48H1437.5H2880V0H2160C1442.5 52 720 0 720 0H0V48Z"
              fill="currentColor"
            ></path>
          </svg>
        </div>
      </div>
      {/* <!-- End Hero --> */}

      <section className="relative lg:py-10 py-10">
        <div className="container">
          <div className="md:flex justify-center">
            <div className="md:w-3/4">
              {creatingPage ? (
                <div className="p-6 bg-white dark:bg-slate-900 shadow dark:shadow-gray-700 rounded-md">
                  <h1 className="" style={{ fontSize: '26px' }}>
                    Create New
                    <button
                      className="btn btn-info btn-sm"
                      onClick={e => setCreateNewPage(!creatingPage)}
                    >
                      Page
                    </button>
                  </h1>
                  <hr />
                  <form onSubmit={e => createPage(e)}>
                    <input
                      required
                      onChange={e => setPage(e.target.value)}
                      className="form-input mb-2 mt-4"
                      placeholder="Enter Page Name [Unique]"
                    />
                    <button className="btn bg-green-600 hover:bg-green-700 text-white rounded-md">
                      Create
                    </button>
                  </form>
                </div>
              ) : (
                <div className="p-6 bg-white dark:bg-slate-900 shadow dark:shadow-gray-700 rounded-md">
                  <h1 className="" style={{ fontSize: '26px' }}>
                    Create New{' '}
                    <button
                      className="btn btn-info btn-sm"
                      onClick={e => setCreateNewPage(!creatingPage)}
                    >
                      Section
                    </button>
                  </h1>
                  <hr />
                  <form onSubmit={e => createSection(e)}>
                    <label htmlFor="">Select Page</label>
                    <select
                      className="form-input mb-2 "
                      onChange={e => setPage(e.target.value)}
                      required
                    >
                      <option value="">Select a Page</option>
                      {Object.keys(content).map(page => (
                        <option value={page}> {page} </option>
                      ))}
                    </select>
                    <input
                      required
                      onChange={e => setSectionName(e.target.value)}
                      className="form-input mb-2"
                      placeholder="Enter Section Name Name [Unique]"
                    />
                    <button className="btn bg-green-600 hover:bg-green-700 text-white rounded-md">
                      Create
                    </button>
                  </form>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
      {/* <!-- Topics --> */}
      <section className="relative lg:py-10 py-10">
        <div className="container">
          <div className="md:flex justify-center mb-4">
            <div className="md:w-3/4">
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                }}
              >
                <h1 style={{ fontSize: '26px' }}> Page Content</h1>
                <button
                  className="btn bg-green-600 hover:bg-green-700 text-white rounded-md"
                  onClick={e => setShowInput(!showInput)}
                >
                  {showInput ? 'Show Content' : 'Show Input For Update'}
                </button>
              </div>
            </div>
          </div>
          {Object.keys(content).map(page => (
            <div className="md:flex justify-center mb-4">
              <div className="md:w-3/4">
                <div className="p-6 bg-white dark:bg-slate-900 shadow dark:shadow-gray-700 rounded-md">
                  <h5
                    style={{ textTransform: 'capitalize' }}
                    className="text-xl font-medium mb-4"
                  >
                    {page}
                  </h5>
                  <hr />
                  <ul className="list-none text-slate-400 mt-3">
                    {content[page].content.map(content => (
                      <div>
                        <li
                          className="flex mt-2"
                          style={{ textTransform: 'capitalize' }}
                        >
                          <i className="uil uil-arrow-right text-green-600 align-middle me-2"></i>
                          {content.title}
                        </li>
                        {showInput ? (
                          <>
                            <hr />
                            <div className="d-flex">
                              {content.img.map(img => (
                                <img
                                  style={{
                                    maxWidth: '200px',
                                    borderRadius: '7px',
                                  }}
                                  src={`${baseURL}/${img}`}
                                />
                              ))}
                            </div>
                            <h3>Text</h3>
                            <hr />
                            <div className="">
                              {content.txt.map(txt => (
                                <p> {txt} </p>
                              ))}
                            </div>
                          </>
                        ) : (
                          <>
                            <div>
                              <div>
                                <div>
                                  <label htmlFor="Image">Image</label>
                                  <input
                                    onChange={e => setImages(e.target.value)}
                                    className="form-input mb-2"
                                    value={
                                      pageName == page &&
                                      sectionName == content.title
                                        ? images
                                        : ''
                                    }
                                  />
                                </div>
                                <div>
                                  <div>
                                      <input
                                        type="file"
                                        accept="image/*"
                                        onChange={onFileChange}
                                      />
                                    {message && <p>{message}</p>}
                                  </div>
                                </div>
                              </div>

                              <br />
                              <label htmlFor="">Text</label>
                              <input
                                onChange={e => setTexts(e.target.value)}
                                className="form-input mb-2"
                                value={
                                  pageName == page &&
                                  sectionName == content.title
                                    ? text
                                    : ''
                                }
                              />
                              <br />
                              <div className="d-flex">
                                <button
                                  className="btn btn-info mr-2 btn-sm"
                                  style={{ marginRight: '20px' }}
                                  onClick={e => {
                                    setImages(content.img);
                                    setTexts(content.txt);
                                    setPage(page);
                                    setSectionName(content.title);
                                  }}
                                >
                                  Set Current Values
                                </button>
                                <button
                                  onClick={e =>
                                    updateSection(page, content.title)
                                  }
                                  className="btn btn-success btn-sm"
                                >
                                  Update
                                </button>
                              </div>
                            </div>
                          </>
                        )}
                      </div>
                    ))}
                  </ul>
                </div>
              </div>
            </div>
          ))}
        </div>
      </section>
      {/* Subscriber */}
      <section className="relative lg:py-10 py-10">
        <div className="container">
          <div className="md:flex justify-center">
            <div className="md:w-3/4">
              <div className="p-6 bg-white dark:bg-slate-900 shadow dark:shadow-gray-700 rounded-md">
                <h5
                  style={{ textTransform: 'capitalize' }}
                  className="text-xl font-medium mb-4"
                >
                  All Subscribers ({allUser.length})
                </h5>
                <hr />
                <ul className="list-none text-slate-400 mt-3">
                  {allUser.map(sub => (
                    <div>
                      <li
                        className="flex mt-2"
                        style={{ textTransform: 'lowercase' }}
                      >
                        <i className="uil uil-arrow-right text-green-600 align-middle me-2"></i>
                        {sub.email}
                      </li>
                    </div>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* <Switcher /> */}
    </>
  );
}
