import React from 'react';
import { Link2 } from 'react-feather';
import { Link } from 'react-router-dom';

const FooterCall = () => {
  return (
    <div className="container " style={{marginTop:"50px", marginBottom:'50px'}}>
      <div className="grid grid-cols-1 text-center">
        <h3 className="mb-6 md:text-3xl text-2xl md:leading-normal leading-normal font-medium text-black dark:text-white">
          Have Question ? Get in touch!
        </h3>

        <p className="text-slate-400 max-w-xl mx-auto">
          Unlock the Power of Your Data with Insightful Analytics
        </p>
        <div className="mt-6"> 
          <Link to={"/contact"}>
          <button className='btn bg-green-600 hover:bg-green-700 text-white rounded-md mt-3'>Contact US</button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default FooterCall;
